* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.nav-app {
  font-family: "Josefin Sans";
  height: 100px;
  background-color: #f7f7f7;
  box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.2);
  position: sticky;
  top: 0;
  z-index: 999;
}

.navigation-logo {
  height: 70px;
}

.logo-link {
  margin-top: 15px;
}

@media screen and (max-width: 899px) {
  .title {
    padding: 50px 0px;
  }

  .menu-link {
    font-size: 19px;
    color: #fff;
  }

  .logo-bars {
    display: flex;
    height: 100%;
    justify-content: space-between;
    margin: 0px 30px;
    z-index: 999;
  }

  .bar {
    height: 3px;
    width: 25px;
    margin: 5px;
    background-color: #282c34;
  }

  .bars {
    margin: auto 0px;
  }

  nav {
    position: fixed;
    top: 0;
    z-index: 999;
    height: 100vh;
    width: 100vw;
    background: url("./Image/bg-menu.png");
    background-color: #282c34;
    transform: translateX(-900px);
  }

  .fa-times-circle {
    position: fixed;
    top: 50px;
    right: 50px;
    font-size: 30px;
    color: #d2d2d2;
  }

  nav ul li {
    margin: 10px 0px;
    list-style: none;
  }

  nav ul {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

@media screen and (min-width: 900px) {
  .title {
    padding: 70px 0px;
  }

  .navigation-desk {
    display: flex;
    justify-content: space-between;
    margin: 0px 100px;
  }

  ul {
    margin: 40px 0px 0px 0px;
  }

  nav ul li {
    display: inline;
    list-style: none;
    margin: 0px 20px;
  }

  .menu-link {
    font-size: 17px;
    color: #282c34;
  }

  .menu-link:hover {
    color: #ef5350;
  }
}

/* Slider */
@media screen and (min-width: 900px) {
  .slider {
    height: 500px;
    color: #282c34;
    background: linear-gradient(to right, #de6262, #ffb88c);
    /* background-position: center; */
    /* background: url("./Image/1931.jpg"); */
    /* background-size: cover; */
  }

  .slider-img {
    position: absolute;
    bottom: 50px;
    right: 100px;
    height: 250px;
  }

  .cta-txt-btn {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    letter-spacing: 1.5px;
    font-family: "Montserrat";
  }

  .Slider-txt {
    width: 600px;
    line-height: 40px;
    font-size: 1.3rem;
    line-height: 40px !important;
  }
}

.cta-btn {
  font-size: 15px;
  padding: 10px 15px;
  background-color: #282c34;
  border-radius: 3px;
  letter-spacing: 1px;
  word-spacing: 3px;
  color: #fff;
}

.Slider-txt {
  line-height: 30px;
  margin-bottom: 50px;
  font-weight: 600;
  color: #444;
}

@media screen and (max-width: 899px) {
  .Slider-txt {
    font-size: 1.1rem;
    color: #f7f7f7;
  }

  .slider {
    height: 50vh;
    z-index: 0;
    background-color: #ef5350;
  }

  .cta-txt-btn {
    letter-spacing: 1px;
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: "Montserrat";
    text-align: center;
    padding: 0px 30px;
  }
}

.title {
  font-family: "Josefin Sans";
  color: #444;
  text-align: center;
}

