/* Client Review */
@media screen and (max-width: 600px) {
  .swiper-button-prev:after,
  .swiper-button-next:after {
    display: none;
  }
}

@media screen and (min-width: 900px) {
  .swiper-button-prev:after,
  .swiper-button-next:after {
    font-size: 20px !important;
    font-weight: 600;
  }
  .slider-wraper {
    text-align: center;
    margin: 0px 100px;
  }

  .review-t {
    margin: 150px 0px 100px 0px;
  }

  .client-review-des {
    padding: 0px 150px;
  }

  .client-name {
    margin: 100px 200px 0px 0px;
    text-align: right;
  }

  .review {
    padding: 50px;
  }
}

@media screen and (max-width: 899px) {
  .review {
    padding: 30px;
  }

  .swiper-pagination-bullets {
    display: none;
  }

  .warn-form {
    text-align: center;
  }

  .swiper-pagination-bullet {
    display: none;
  }

  .review-t {
    margin: 100px 0px;
  }

  .client-review-des {
    padding: 0px 20px;
  }

  .client-name {
    margin: 20px auto 0px auto;
  }
}

