/* card_port */
.card_port {
  height: 200px;
  width: 250px;
  background-color: #ff7260;
  color: #f7f7f7;
  font-family: "Josefin Sans";
  cursor: pointer;
  box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.2);
  transition: 0.5s;
  border-radius: 5px;
  display: inline-block;
  margin: 30px;
}
@media screen and (max-width: 899px) {
  .card_port {
    display: block;
    margin: 30px auto;
  }
}
.card_port:hover {
  transform: scale(1.1);
  transition: 0.5s;
}
.card_port p {
  letter-spacing: 1px;
  position: relative;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
